import React, { Component } from "react";
import Styles from "./index.module.css";
// 评分体系
import Star from "../../component/star";
// 评价组件
import Reviews from "../../component/reviews";
// 数量计算组件
import OrderCalc from "../../component/orderCalc";
import { Carousel, Toast } from "antd-mobile";
import { connect } from "dva";
import { strip } from "../../tools/number-precision";

class Index extends Component {
  constructor(props) {
    super(props);
    let foodItemId = this.props.foodItemId;
    this.state = {
      foodItemId
    };
  }
  componentWillMount() {
    Toast.loading("Loading...");
  }
  componentDidMount() {
    let { foodItemId } = this.state;
    this.getFoodItemsDetail(foodItemId);
    this.reviewsFoodItem(foodItemId).then(() => {
      Toast.hide();
    });
    this.foodItemPicGet();
  }
  // 获取图片列表
  foodItemPicGet() {
    let { getFoodItemsDetail = {} } = this.props.orders;
    this.props
      .dispatch({
        type: "orderInfo/foodItemPicGet",
        payload: {
          foodItemId: this.state.foodItemId
        }
      })
      .then((pictureUrl = []) => {
        let { foodItem = {} } = getFoodItemsDetail;
        if (foodItem.primaryPictureUrl) {
          pictureUrl.unshift({ pictureUrl: foodItem.primaryPictureUrl });
        }
        this.setState({
          pictureUrl
        });
      });
  }
  // 菜品信息
  getFoodItemsDetail = foodItemId => {
    return this.props.dispatch({
      type: "orders/getFoodItemsDetail",
      payload: {
        foodItemId
      }
    });
  };
  // 菜品评价
  reviewsFoodItem = foodItemId => {
    return this.props.dispatch({
      type: "orderInfo/reviewsFoodItem",
      payload: {
        foodItemId
      }
    });
  };
  hocLink = () => {
    window.history.go(-1);
  };
  // orderNumChange
  orderChange = foodItemId => {
    return num => {
      this.props.dispatch({
        type: "orders/orderNumChange",
        payload: {
          foodItemId,
          num
        }
      });
    };
  };
  render() {
    const { reviewsFoodItemList = {} } = this.props.orderInfo;
    let { getFoodItemsDetail = {} } = this.props.orders;
    let { foodItem = {} } = getFoodItemsDetail;
    const buildImgs = () => {
      let arra = this.state.pictureUrl || [];
      return arra.map((item = {}, index) => {
        let src = item.pictureUrl;
        return (
          <a key={index} href="javascrip:;" className={Styles.bannerList}>
            <img src={src} alt="" />
          </a>
        );
      });
    };
    return (
      <div className={Styles.info}>
        {/* banner */}
        <div className={Styles.banner}>
          {/* banner 渲染 */}
          <Carousel autoplay={false} infinite>
            {buildImgs()}
          </Carousel>
        </div>
        <div className={Styles.info_body}>
          <div className={Styles.info_title}>
            <span>{foodItem.name}</span>
          </div>
          <div className={Styles.info_content}>
            <div className={Styles.info_content_left}>
              <div className={Styles.info_price}>
                <span>
                  $
                  {foodItem.dailySpecialPrice
                    ? foodItem.dailySpecialPrice
                    : foodItem.price}
                </span>
                {!!foodItem.dailySpecialPrice && (
                  <span className={Styles.content_card_originPrice}>
                    ${foodItem.price}
                  </span>
                )}
                {/* <span>after 10% off</span> */}
              </div>
              <div className={Styles.info_star}>
                <Star
                  totalReviewScore={foodItem.totalReviewScore}
                  reviewTimes={foodItem.reviewTimes}
                />
                {foodItem.reviewTimes ? (
                  <span>
                    {strip(foodItem.totalReviewScore / foodItem.reviewTimes, 2)}
                  </span>
                ) : (
                  <span>0.0</span>
                )}
                <span>{getFoodItemsDetail.orderTimes} sold</span>
              </div>
            </div>
            {/* 数量计算组件 */}
            <OrderCalc
              value={foodItem.num}
              onChange={this.orderChange(foodItem.foodItemId)}
            />
          </div>
          <div className={Styles.info_footer}>
            <span className={Styles.info_footer_subtitle}>Ingredients</span>
            <p>
              {foodItem.complementaryIngredient || foodItem.primaryIngredient}
            </p>
          </div>
          <div className={Styles.info_reviews}>Reviews</div>
          <Reviews reviews={reviewsFoodItemList} reviewStr="score" />
          <div className={Styles.footer} onClick={this.props.close}>
            <div className={Styles.cart}>
              <span>Back to menu</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(state => {
  return {
    orderInfo: state.orderInfo,
    orders: state.orders
  };
})(Index);
