import React, { Component } from "react";
import moment from "moment-timezone";
import Styles from "./index.module.css";
import Star from "./star";
class Index extends Component {
  render() {
    const reviews = this.props.reviews || [];
    const buildImg = (pictureUrls = []) => {
      return pictureUrls.map((item, index) => {
        return (
          <div key={index}>
            <img src={item} alt="" />
          </div>
        );
      });
    };
    const buildCard = () => {
      let arra = [];
      reviews.forEach((item, index) => {
        let { review = {} } = item;
        if (!review.reviewTime) {
          review = item.foodItemReview;
        }
        arra.push(
          <div className={Styles.reviews_card} key={index}>
            <div className={Styles.reviews_header}>
              {item.customerAvatarPictureUrl && (
                <img src={item.customerAvatarPictureUrl} alt="" />
              )}
              <div className={Styles.reviews_info}>
                <span>{item.customerUserName}</span>
                <div style={{ marginTop: "0.05rem" }}>
                  <Star
                    score={review[this.props.reviewStr || "comprehensiveScore"]}
                  />
                </div>
              </div>
              <span>
                {moment(review.reviewTime || review.reviewAt)
                  .utcOffset(1)
                  .format("HH:mm:ss DD/MM/YYYY")}
              </span>
            </div>
            <p className={Styles.reviews_message}>{review.scoreContent}</p>
            <div className={Styles.reviews_imgs}>
              {buildImg(item.pictureUrls)}
            </div>
          </div>
        );
      });
      for (let i = 0; i < 3; i++) {}
      return arra;
    };
    return <div>{buildCard()}</div>;
  }
}

export default Index;
